html{font-size:100%;box-sizing:border-box}*,*::before,*::after{box-sizing:inherit}body{margin:0;padding:0;font-family:"Noto Sans HK",sans-serif;line-height:1.5;background-color:#C2B092}h1,h2,h3{margin:0}h1{font-weight:300;text-align:left}.container{padding:0 1.875rem}a,a:visited,a:active{text-decoration:none}@media (max-width: 63.9375em){.hide-for-mobile{display:none}}@media (min-width: 64em){.hide-for-desktop{display:none}}.hide{display:none}

.header{position:fixed;top:0;left:0;min-width:100vw;min-height:10vh;background-color:#A1CAE2;box-shadow:1px 1px 15px #8080809e;display:flex;justify-content:space-around;align-items:center}@media (min-width: 64em){.header{min-width:20vw;min-height:100vh;display:grid;grid-template-rows:1fr 2fr 1fr}}.header__toggle{margin-top:5px}.header__toggle span{display:block;width:2rem;height:5px;margin-top:5px;background-color:white;transition:all 300ms ease-in-out;transform-origin:1px 2px}.header__toggle:not(:last-child){margin-bottom:6px}.header__toggle.open span:first-child{transform:rotate(45deg)}.header__toggle.open span:nth-child(2){opacity:0}.header__toggle.open span:last-child{transform:rotate(-45deg)}.header h1{color:white;text-align:center}.header__overlay{display:none}.header__overlay.open{position:fixed;background-color:#A1CAE2;left:0;top:10%;bottom:0;min-height:100vh;min-width:60vw;padding:1rem;border-top:1px solid white;text-align:center;display:flex;flex-direction:column}@media (min-width: 64em){.header__overlay.open{display:none}}@media (min-width: 64em){.header__nav{display:flex;flex-direction:column;text-align:center}}

.link{color:white;padding:1rem;font-weight:500;font-size:0.875rem}.link:hover{color:#EAE3C8}.button{background-color:#A1CAE2;color:white;border:3px solid #EAE3C8;border-radius:10px;height:3.75rem;width:12.5rem;font-size:.875rem;font-weight:500}.button:hover{color:#EAE3C8}.input{border-bottom:1px solid #C2B092;margin-bottom:1.5rem;display:flex}.input__box{flex:1 1;border:none;font-size:1rem;margin-left:0.5rem;padding:5px}@media (min-width: 64em){.input__box{padding:0.5rem;margin-left:1rem}}.card{background-color:white;border-radius:5px;margin-bottom:1.875rem;padding:0.875rem;display:flex;flex-direction:column;justify-content:center;z-index:1}.card.hide{display:none}.card h2{text-align:center;border-bottom:1px solid #C2B092;width:50%;margin-bottom:1rem;align-self:center}@media (min-width: 64em){.card{border-radius:20px;padding:1.875rem 5rem;max-width:40rem}.card h2{margin-bottom:2rem}}.card span{font-size:1rem;align-self:flex-end}.card span:hover{color:#C2B092;cursor:pointer}

.main{margin:8rem 0.875rem}@media (min-width: 40em){.main{margin:8rem 5rem}}@media (min-width: 64em){.main{margin-left:35vw;display:flex;justify-content:center;flex-direction:column}}.preview__container{background-color:white;border-radius:10px;min-height:50vh;display:flex;flex-direction:column;margin-bottom:2rem}@media (min-width: 64em){.preview__container{max-width:50vw;padding:3.5rem 3rem}}.preview h2{text-align:center;border-bottom:1px solid #EAE3C8}.preview__section h3{color:#C2B092;padding-top:1rem}.preview__section hr{border-top:1px solid #EAE3C8}.preview__section p{margin:0}.preview__section.contact-section{display:flex;flex-direction:row;justify-content:space-between;padding-top:1rem}.preview hr{border-top:1px solid #EAE3C8;margin-bottom:0.5rem}.preview__card p:first-of-type{font-size:larger}.preview__update{float:right;cursor:pointer;color:#C2B092}.preview__update:hover{color:#EAE3C8}.preview span:nth-of-type(2){padding-right:10px}.preview__buttons{display:flex;flex-direction:row}.preview__buttons button:not(:last-of-type){margin-right:1rem}

